.social-links {
  position: absolute;
  top: 0;
  right: 0;
}

.social-icon {
  display: inline-block;
  height: 60px;
  width: 60px;
  margin-top: 30px;
  margin-left: 10px;
  cursor: pointer;
}

.social-icon-img {
  margin-top: -30px;
  margin-left: -30px;
  height: 120px;
  pointer-events: none;
}
